import React, { PureComponent } from 'react';
import { PieChart, Pie, Sector, Cell } from 'recharts';
import { withTranslation } from 'react-i18next';
import { t } from '../../i18n';

import ResponsiveContainer from './component/ResponsiveContainer';

import { COLOR_PANEL } from './util/Constants';

const renderActiveShape = props => {
  const RADIAN = Math.PI / 180;
  const {
    cx,
    cy,
    midAngle,
    innerRadius,
    outerRadius,
    startAngle,
    endAngle,
    fill,
    payload,
    percent,
    value,
  } = props;
  const sin = Math.sin(-RADIAN * midAngle);
  const cos = Math.cos(-RADIAN * midAngle);
  const sx = cx + (outerRadius + 10) * cos;
  const sy = cy + (outerRadius + 10) * sin;
  const mx = cx + (outerRadius + 30) * cos;
  const my = cy + (outerRadius + 30) * sin;
  const ex = mx + (cos >= 0 ? 1 : -1) * 22;
  const ey = my;
  const textAnchor = cos >= 0 ? 'start' : 'end';

  return (
    <g>
      <text x={cx} y={cy} dy={8} textAnchor="middle" fill={fill}>
        {formattedAccountNumber(payload.AccountNumber)}
      </text>
      <Sector
        cx={cx + 2}
        cy={cy + 2}
        innerRadius={innerRadius}
        outerRadius={outerRadius + 10}
        startAngle={startAngle}
        endAngle={endAngle}
        fill={fill}
      />
      <Sector
        cx={cx + 2}
        cy={cy + 2}
        startAngle={startAngle}
        endAngle={endAngle}
        innerRadius={outerRadius + 16}
        outerRadius={outerRadius + 20}
        fill={fill}
      />
      <path
        d={`M${sx},${sy}L${mx},${my}L${ex},${ey}`}
        stroke={fill}
        fill="none"
      />
      <circle cx={ex} cy={ey} r={2} fill={fill} stroke="none" />
      <text
        x={ex + (cos >= 0 ? 1 : -1) * 12}
        y={ey}
        textAnchor={textAnchor}
        fill={fill}
      >{`${t('Components:balance')}: ${formattedNumbersWithCurrency(
        value,
      )}`}</text>
      <text
        x={ex + (cos >= 0 ? 1 : -1) * 12}
        y={ey}
        dy={18}
        textAnchor={textAnchor}
        fill="#999"
      >
        {`(${t('Components:rate')}: ${(percent * 100).toFixed(2)}%)`}
      </text>
    </g>
  );
};
const formattedNumbersWithCurrency = number => {
  let formattedNumber = number.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ' ');
  formattedNumber = formattedNumber.concat(`  ${t('Components:currency')}`);
  return formattedNumber;
};
const formattedAccountNumber = number => {
  let formattedNumber = 'N°'.concat(number);
  return formattedNumber;
};

class Chart extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      data: this.props.data,
      animation: true,
      top: 'dataMax+100000',
      bottom: 'dataMin-100000',
      activeIndex: 0,
    };
  }

  handleClick = (data, index) => {
    this.setState({
      activeIndex: index,
    });
  };
  onPieEnter = (data, index) => {
    this.setState({
      activeIndex: index,
    });
  };
  render() {
    const { data, activeIndex } = this.state;
    return (
      <div
        style={{ height: 500, overflow: 'auto' }}
        className="col-md-7 col-xs-12 table-liste-operation"
      >
        <ResponsiveContainer width="100%" height="100%">
          <PieChart>
            <Pie
              activeIndex={activeIndex}
              activeShape={renderActiveShape}
              data={data}
              dataKey="Solde"
              nameKey="AccountName"
              innerRadius={70}
              fill="#82ca9d"
              onMouseEnter={this.onPieEnter}
              paddingAngle={2}
            >
              {data.map((entry, index) => (
                <Cell
                  key={`cell-${index}`}
                  fill={COLOR_PANEL[index % COLOR_PANEL.length]}
                />
              ))}
            </Pie>
          </PieChart>
        </ResponsiveContainer>
      </div>
    );
  }
}
export default withTranslation()(Chart);
