import React from 'react';
import { t } from '../../../i18n';
import { DASH_NAMESPACE } from '../constants';
import Chart from '../../../components/Chart/ProvChart';
import Panel from '../../../components/Panel/Panel';
import Legend from './legend';

const chartPanel = props => (
  <Panel title={t(`${DASH_NAMESPACE}globalSituation`)}>
    {props.chartData.length > 0 ? (
      <div className="row">
        <Chart data={props.chartData} />
        <Legend data={props.chartData} />
      </div>
    ) : (
      <div />
    )}
  </Panel>
);
export default chartPanel;
