import React, { Component } from 'react';
import Slider from 'react-slick';
import { withTranslation } from 'react-i18next';
import image1 from '../../../assets/img/DashboardSlider/image1.jpg';
import image2 from '../../../assets/img/DashboardSlider/image2.jpg';
import image3 from '../../../assets/img/DashboardSlider/image3.jpg';
class DashboardSlider extends Component {
  render() {
    const settings = {
      dots: true,
      infinite: true,
      arrows: false,
      speed: 500,
      slidesToShow: 1,
      slidesToScroll: 1,
      fade: true,
      autoplay: true,
    };
    return (
      <Slider className="slider-dashboard" {...settings}>
        <div className="slide-list">
          <div className="content">
            <div className="slider-image">
              <img src={image1} alt="Atlantique Net" />
            </div>
          </div>
        </div>
        <div className="slide-list">
          <div className="content">
            <div className="slider-image">
              <img src={image2} alt="Atlantique Net and Atlantique Mobile" />
            </div>
          </div>
        </div>
        <div className="slide-list">
          <div className="content">
            <div className="slider-image">
              <img src={image3} alt="Atlantique Net and Atlantique Mobile" />
            </div>
          </div>
        </div>
      </Slider>
    );
  }
}

export default withTranslation()(DashboardSlider);
