import React from 'react';
import { t } from '../../../i18n';
import { DASH_NAMESPACE } from '../constants';
import ChartPanel from './chartPanel';
import DashboardSlider from './DashboardSlider';

class Feature extends React.PureComponent {
  componentDidMount() {
    if (this.props.fullName === '') this.props.getSignaletic();
  }
  render() {
    return (
      <div className="page-content">
        <div className="page-head">
          <div className="page-head-compte">
            <div className="left_title_dashboard left-col">
              <div className="left__top col-bienvenue">
                <div className="profile_img_zone">
                  <img
                    src={require('../../../assets/img/logos/logo_anet.png')}
                    alt="loganet"
                  />
                </div>
                <h2 className="page-head-title">
                  {`${t(`${DASH_NAMESPACE}hello`)} ${this.props.fullName}`}
                </h2>
              </div>
            </div>
          </div>
        </div>
        <div className="main-content container-fluid">
          <div className="row mx-auto">
            <div className="col-sm-12">
              <ChartPanel chartData={this.props.chartData} />
            </div>
            <div className="col-sm-12">
              <DashboardSlider />
            </div>
          </div>
        </div>
      </div>
    );
  }
}
export default Feature;
