import React from 'react';
import { withTranslation } from 'react-i18next';
import { connect } from 'react-redux';
import { setPickedAccount } from '../../../redux/operations/operations.actions';
import Surface from '../../../components/Chart/container/Surface';
import { DASH_NAMESPACE } from '../constants';
import { COLOR_PANEL } from '../../../components/Chart/util/Constants';
import { t } from '../../../i18n';
import FormattedNumber from '../../../components/FormattedNumber/FormattedNumber';
import FormatsConfiguration from '../../../utils/FormatsConfiguration';
import FormModal from '../../../components/Ui/CustomModal/FormModal';
import OperationHistory from './operationHistoryPanel';

class Legend extends React.PureComponent {
  state = {
    showModal: false,
  };

  _handleClose = () => {
    this.setState({ showModal: false });
  };
  render() {
    const SIZE = 12;
    const iconSize = 12;
    const viewBox = { x: 0, y: 0, width: SIZE, height: SIZE };
    const svgStyle = {
      display: 'inline-block',
      verticalAlign: 'middle',
      marginRight: 4,
    };
    return (
      <div className="recharts-legend-wrapper col-md-5 col-xs-12">
        <h3>{t(`${DASH_NAMESPACE}accountList`)}</h3>
        <table>
          <tbody>
            {this.props.data.map((element, index) => (
              <tr>
                <td width="20">
                  <div
                    className="bg-colonne"
                    style={{
                      borderRight: `4px solid ${COLOR_PANEL[index]}`,
                    }}
                  ></div>
                  <Surface
                    width={iconSize}
                    height={iconSize}
                    viewBox={viewBox}
                    style={svgStyle}
                  >
                    <path
                      stroke="none"
                      fill={COLOR_PANEL[index]}
                      d={`M0,${SIZE / 8}h${SIZE}v${(SIZE * 3) / 4}h${-SIZE}z`}
                      className="recharts-legend-icon"
                    />
                  </Surface>
                </td>
                <td>
                  <span className="name">{element.AccountName}</span>
                  <span className="num">
                    {t(`${DASH_NAMESPACE}number`)}
                    {element.AccountNumber}
                  </span>
                </td>

                <td className="align_right">
                  <span className="size16 uppercase_txt hard_grey_txt bold evolutionCompteSolde">
                    <FormattedNumber
                      number={element.Solde}
                      format={FormatsConfiguration.AmountFormat}
                      currency={t(`${DASH_NAMESPACE}currency`)}
                    />
                  </span>
                </td>
                <td className="align_right">
                  <button
                    className="modal-close btn-secondary btn md-trigger popin_btn"
                    onClick={() => {
                      this.props.setPickedAccount(element.accountId);
                      this.setState({
                        showModal: true,
                      });
                    }}
                  >
                    <span>{t(`${DASH_NAMESPACE}seeLastOperations`)}</span>
                  </button>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
        <FormModal
          handleClose={this._handleClose}
          show={this.state.showModal}
          title={t(`${DASH_NAMESPACE}lastOperations`)}
        >
          <OperationHistory accountsData={this.props.data} />
        </FormModal>
      </div>
    );
  }
}

const mapDispatchToProps = dispatch => ({
  setPickedAccount: accountId => {
    dispatch(setPickedAccount(accountId));
  },
});
export default withTranslation()(
  connect(
    null,
    mapDispatchToProps,
  )(Legend),
);
